import React from "react";
import "./Footer.css";
function Footer({lang}) {
  return (
    <div className="container Footer">
      <div className="row py-4">
        <div className="col-lg-4">
        © 2022 Miguel González. All rights reserved.
        </div>
        <div className="col-lg-8">
          <div className="row d-flex justify-content-end">
            <div className="col-lg-4">{lang?'8 Razones para trabajar con Miguel':'8 Reasons to work with Miguel'}</div>
            <div className="col-lg-2">{lang?'Aviso Lega':'Legal Notice'}</div>
            <div className="col-lg-2">{lang?'Privacidad':'Privacy Policy'}</div>
            <div className="col-lg-2">{lang?' Cookies':'Cookie Policy'}</div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;
