import React, { useState } from "react";
import "./Header.css";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
//   import { NavLink, useLocation } from "react-router-dom";
function Header({lang}) {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  return (
    <div>
      <Navbar expand={"lg"} container="fluid " className="py-1" color={"dark"} onClick={toggle}>
        {/* <NavbarToggler onClick={toggle}></NavbarToggler> */}
        <div className='col-lg '>
            <NavItem>
              <NavLink onClick={toggle} className={"nav-link order-0 text-lg-end"} to={"/nosotros"}>
                {" "}
           {     <h6>Miguel González</h6>}
              </NavLink>
            </NavItem>
            </div>
        <Collapse  isOpen={isOpen} navbar className='col-lg-3 order-2  order-lg-1 ' >
          <Nav className=" d-flex align-items-between align-items-center w-100 flex-row" navbar>
            <div className='col-lg d-lg-flex justify-content-lg-center'>
            <NavItem>
              <NavLink onClick={toggle} className={"nav-link text-lg-center"} to={"/nosotros"}>
                {" "}
             { lang?  <h6>Sobre Mí</h6>: <h6>About</h6>}
              </NavLink>
            </NavItem>
            
            <NavItem>
              <NavLink onClick={toggle} className={"nav-link text-lg-center"} to="/familias">
                {" "}
              { lang ?<h6>Mis Habilidades</h6>:<h6>My Skill</h6>}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink onClick={toggle} className={"nav-link text-lg-center"} to="/educadores">
                {" "}
             {lang?   <h6>Trabajo</h6>:<h6>Work</h6>}
              </NavLink>
            </NavItem>
            </div>
            
          </Nav>
        </Collapse>
        <div  className='col-lg order-1 order-lg-2'>
            <NavItem>
              <NavLink onClick={toggle} className={"nav-link"} to="/contacto">
                {" "}
                <h6> <span className='blue__color'>{lang?'Contacto >':'Contect >'}  </span></h6>
              </NavLink>
            </NavItem>
            </div>
      </Navbar>
    </div>
  );
}

export default Header;
